.sameHeight {
  /* display: flex; */
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: stretch;
  text-align: justify;
  /* height: 1320px; */
}

.wrap-content{
  margin-top: 5px;
  margin-bottom: 5px;
}

.avatarImg img{
  display: block;
  margin: 0 auto;
  margin-top: 50px;
  max-width: 280px;
}
.social i{
  padding: 2px;
  color: rgb(68,68,68);
}

.mt{
  margin-top: 1px;
}

.pt {
  padding-top: 1px;
}

h5 {
  font-size: 30px;
  padding-bottom: 1px;
}

h6 span {
  float: right;
  font-size: 14px;
}


.card-content.social{
  padding-top: 1px;
  padding-bottom: 1px;
}

@media only screen and (max-width: 992px) {
  .social i {
    padding: 2px;
    color: #fff;
  }
}

@media only screen and(max-width: 420px){
  h2 {
    font-size: 30px;
  }
  h5 {
    font-size: 18px;
    padding-bottom: 10px;
  }
  h6{
    padding-bottom: 15px;
    line-height: 140%;
  }
  h6 strong {
    display: block;
  }
  h6 span {
    float: left;
    font-size: 90%;
  }
  .card-content.social{
    width: 700px;
  }
} 

